import Vue from 'vue'
import Router from 'vue-router'
import main from '@/components/main'
// import home from '@/components/home'
import firstmenu from '@/components/menu'
import secondmenu from '@/components/secondmenu'
import thirdmenu from '@/components/thirdmenu'
import fourthmenu from '@/components/fourthmenu'
import chartsdata from '@/components/chartsData'
import card from '@/components/card'
import report from '@/components/report'
import mine from '@/components/mine'
import zhibiao from '@/components/zhibiao'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/firstmenu',
      name: 'mainView',
      component: main,
      children:[
        {
          path: '/firstmenu',
          name: 'firstmenu',
          component: firstmenu,
        },
        {
          path: '/secondmenu',
          name: 'secondMenu',
          component: secondmenu,
        },
        {
          path: '/thirdmenu',
          name: 'thirdMenu',
          component: thirdmenu,
        },
        {
          path: '/fourthmenu',
          name: 'fourthMenu',
          component: fourthmenu,
        },
        {
          path: '/chartsdata',
          name: 'chartsdata',
          component: chartsdata,
        },
        {
          path: '/card',
          name: 'card',
          component: card,
        },
        {
          path: '/report',
          name: 'report',
          component: report,
        },
        {
          path: '/mine',
          name: 'mine',
          component: mine,
        },
        {
          path: '/zhibiao',
          name: 'zhibiao',
          component: zhibiao,
        },
      ]
    }
  ]
})



