<template>
    <div id="charts1">
        <div class="chart" style="width:100%;height:auto;">
            <div class="shaixuan_box">
                <!-- <div style="display:inline-block;">
                    <span>筛选：</span>
                    <el-select v-model="value[0].val" placeholder="请选择" style="width:120px;" @change="getValue" size="small">
                        <el-option
                        v-for="item in options[0].data"
                        :key="item.key"
                        :label="item.label"
                        :value="item.value">
                        </el-option>
                    </el-select>
                </div> -->
                <div style="display:inline-block;visibility:hidden">
                    <el-select v-model="value[0].val" placeholder="请选择" style="width:120px;" @change="getValue" size="small">
                        <el-option
                        v-for="item in options[0].data"
                        :key="item.key"
                        :label="item.label"
                        :value="item.value">
                        </el-option>
                    </el-select>
                </div>
            </div>
            <div class="back" @click="backcharts()" ref="back">返回</div>
            <div :id="op.id" ref="myChart" style="width:100%;height:350px;margin:0 auto;"></div>
            <div class="table" v-if="table">
                <div class="tabletit">{{tabletitle1}}</div>
                <el-table :data="tableData1.slice((currpage - 1) * pagesize, currpage * pagesize)" style="width: 100%">
                    <el-table-column fixed prop="date" label="日期" width="150"></el-table-column>
                    <el-table-column prop="name" label="姓名"></el-table-column>
                    <el-table-column prop="province" label="省份"></el-table-column>
                    <el-table-column prop="city" label="市区" width="120"></el-table-column>
                    <el-table-column prop="address" label="地址" width="300"></el-table-column>
                    <el-table-column prop="zip" label="邮编" width="120"></el-table-column>
                </el-table>
                <el-pagination background 
                    layout="prev, pager, next, sizes, total"
                    :page-sizes="[5, 10]"
                    :page-size="pagesize"
                    :total="tableData1.length"
                    @current-change="handleCurrentChange" 
                    @size-change="handleSizeChange"
                    style="margin-top:10px;">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
import { Button,Toast } from 'mint-ui';
export default {
    name: 'charts4',
    props:['op'],
    data(){
        return{
            backbtn: null,
            table: false,
            tapnum: 0,      //下钻点击次数，无数据情况下测试表格用
            list: [],
            pagesize: 10,
            currpage: 1,
            tabletitle1: '表格标题1',
            tableData1: [{
                date: '2016-05-03',
                name: '王小虎王小虎',
                province: '上海',
                city: '普陀区',
                address: '上海市普陀区金沙江路 1518 弄',
                zip: 200333
            }, {
                date: '2016-05-02',
                name: '王小虎',
                province: '上海',
                city: '普陀区',
                address: '上海市普陀区金沙江路 1518 弄',
                zip: 200333
            }, {
                date: '2016-05-04',
                name: '王小虎',
                province: '上海',
                city: '普陀区',
                address: '上海市普陀区金沙江路 1518 弄',
                zip: 200333
            }, {
                date: '2016-05-01',
                name: '王小虎',
                province: '上海',
                city: '普陀区',
                address: '上海市普陀区金沙江路 1518 弄',
                zip: 200333
            }, {
                date: '2016-05-08',
                name: '王小虎',
                province: '上海',
                city: '普陀区',
                address: '上海市普陀区金沙江路 1518 弄',
                zip: 200333
            }, {
                date: '2016-05-06',
                name: '王小虎',
                province: '上海',
                city: '普陀区',
                address: '上海市普陀区金沙江路 1518 弄',
                zip: 200333
            }, {
                date: '2016-05-07',
                name: '王小虎',
                province: '上海',
                city: '普陀区',
                address: '上海市普陀区金沙江路 1518 弄',
                zip: 200333
            }],
            option: {
                color: ['#029348'],
                title: {
                    text: '',
                    left:'center'
                },
                tooltip : {
                    trigger: 'axis',
                    axisPointer : {            // 坐标轴指示器，坐标轴触发有效
                        type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                    }
                },
                xAxis : [
                    {
                        type : 'value',
                        // name: '(%)'
                    }
                ],
                yAxis : [
                    {
                        type : 'category',
                        triggerEvent:true,
                        nameLocation: 'middle',
                        // data : ['前一周','前两周','前三周'],
                        axisTick: {
                            alignWithLabel: true,
                            inside: true,
                        },
                        axisLabel: {
                            show: true,
                            interval:0,
                            fontSize:10,
                            align: 'center',
                            padding: [0,20,0,0],
                            formatter:function(params) {
                                var newParamsName = "";
                                var paramsNameNumber = params.length;
                                var provideNumber = 4;
                                var rowNumber = Math.ceil(paramsNameNumber / provideNumber);
                                if (paramsNameNumber > provideNumber) {
                                    for (var p = 0; p < rowNumber; p++) {
                                        var tempStr = "";
                                        var start = p * provideNumber;
                                        var end = start + provideNumber;
                                        if (p == rowNumber - 1) {
                                            tempStr = params.substring(start, paramsNameNumber);
                                        } else {
                                            tempStr = params.substring(start, end) + "\n";
                                        }
                                        newParamsName += tempStr;
                                    }

                                } else {
                                    newParamsName = params;
                                }
                                return newParamsName
                            },
                        },
                    }
                ],
                series : [
                    {
                        // name:'直接访问',
                        // type:'bar',
                        // barWidth: '60%',
                        // data:[80,50,20],
                        itemStyle: {
                            normal: {
                                label: {
                                    show: true,     //开启显示
                                    position: 'right',  //在右方显示
                                    textStyle: {        //数值样式
                                        color: '#029348',
                                        fontSize: 10
                                    }
                                }
                            }
                        },
                    }
                ]
            },
            options:[
                {
                    data:[{
                        key:'1',
                        value: [],
                        label: '全部'
                    },{
                        key:'2',
                        value: [],
                        label: '专业'
                    }]
                }
            ],
            value:[{
                val:'全部',
            }]
        }
    },
    methods: {
        getValue(value){
            console.log(value)
            var mychart = this.$echarts.init(document.getElementById(value[0]))
            
        },
        handleClick(tab, event) {
            // console.log(tab, event);
        },
        formatter(row, column) {
            return row.address;
        },
        handleCurrentChange(cpage) {
            this.currpage = cpage;
        },
        handleSizeChange(psize) {
            this.pagesize = psize;
        },
        drawCharts(){
            let myChart = this.$echarts.init(document.getElementById(this.op.id))
            myChart.setOption(this.option,true)
        },
        backcharts(){
            var _this = this
            _this.table = false
            _this.tapnum = 0
            let myChart = this.$echarts.init(document.getElementById(this.op.id));
            this.option.title.text = this.op.title.text
            this.option.xAxis[0].name = this.op.xAxis[0].name
            this.option.yAxis[0].data = this.op.yAxis[0].data
            this.option.series[0].name = this.op.series[0].name
            this.option.series[0].type = this.op.series[0].type
            this.option.series[0].barWidth = this.op.series[0].barWidth
            this.option.series[0].data = this.op.series[0].data
            this.drawCharts()
            var backbtn = document.getElementsByClassName('back')
            backbtn[_this.op.num-1].style.display = 'none'
        },
    },
    mounted(){
        console.log(this.op)
        for(var i = 0;i < this.options.length;i++){     //将每个图表的id都赋值给options
            for(var j = 0;j < this.options[i].data.length;j++){
                this.options[i].data[j].value[0] = this.op.id
            }
        }
        this.option.title.text = this.op.title.text
        this.option.xAxis[0].name = this.op.xAxis[0].name
        this.option.yAxis[0].data = this.op.yAxis[0].data
        this.option.series[0].name = this.op.series[0].name
        this.option.series[0].type = this.op.series[0].type
        this.option.series[0].barWidth = this.op.series[0].barWidth
        this.option.series[0].data = this.op.series[0].data
        this.drawCharts()
        let myChart = this.$echarts.init(document.getElementById(this.op.id));
        var _this = this
        myChart.on('click',function(params){
            _this.tapnum++
            console.log(_this.tapnum,params)
            var backbtn = document.getElementsByClassName('back')
            backbtn[_this.op.num-1].style.display = 'block'
            if(_this.tapnum == 2){      //无数据情况下测试用，填充数据时删掉，只保留else里面的if语句
                _this.table = true
            }else{
                if(params.componentType == 'yAxis'){
                    _this.option.yAxis[0].data = ['2016','2017','2018','2019']
                    _this.option.series[0].data = [152,45,201,95]
                    myChart.setOption(_this.option,true)
                }else if(params.componentType == 'series'){
                    _this.option.yAxis[0].data = ['2016','2017','2018','2019']
                    _this.option.series[0].data = [152,45,201,95]
                    myChart.setOption(_this.option,true)
                }
            }
            
        })
    }
}
</script>

<style>
#charts1{
    width: 100%;
    height: auto;
}
#charts1 .chart{
    position: relative;
}
#charts1 .chart .shaixuan_box{
    margin: 10px;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
}
#charts1 .chart .shaixuan_box>div{
    margin-top: 10px;
}
#charts1 .back{
    position: absolute;
    right: 7%;
    top: 78px;
    padding: 4px 10px;
    border-radius: 4px;
    color: #ffffff;
    font-size: 14px;
    background: #26a2ff;
    z-index: 100;
    display: none;
}
#charts1 .back:active{
    background: #eeeeee;
}
</style>



