<template>
    <div id="report">
        <div class="bxln_bxdw zwzd">
            <div class="tabletitle">{{title}}</div>
            <div class="btn">
                <div class="fenpei">
                    <span class="name" style="font-size:14px;">分配给：</span>
                    <el-button class="s_btn" @click="fenpei(name1[0])" size="small" type="primary">{{name1[0].name}}</el-button>
                </div>
                <el-button class="search_btn" @click="searchData()" size="small" type="primary">查询数据</el-button>
            </div>
            <div class="table" v-if="zonghe">
                <div class="zdyj">
                    <div class="tit">综合诊改结论</div>
                    <span class="count">({{zhContent.jielun.length}}/500字)</span>
                    <div class="input_box"><textarea v-model="zhContent.jielun" name="jielun" id="" class="text_box" placeholder="重在突出存在问题与原因分析，建议不超过五百字"></textarea></div>
                </div>
                <div class="gjcs">
                    <div class="tit">改进工作建议</div>
                    <span class="count">({{zhContent.jianyi.length}}/200字)</span>
                    <div class="input_box"><textarea v-model="zhContent.jianyi" name="jianyi" id="" class="text_box" placeholder="需突出针对性、注重可行性，建议不超过二百字"></textarea></div>
                </div>
            </div>
            <div class="table" v-else>
                <div class="zdyj">
                    <div class="tit">自我诊断意见</div>
                    <span class="count">({{content.yijian1.length}}/500字)</span>
                    <div class="input_box"><textarea v-model="content.yijian1" name="yijian1" id="" class="text_box" placeholder="重在突出存在问题与原因分析，建议不超过五百字"></textarea></div>
                </div>
                <div class="gjcs">
                    <div class="tit">改进措施</div>
                    <span class="count">({{content.cuoshi1.length}}/200字)</span>
                    <div class="input_box"><textarea v-model="content.cuoshi1" name="cuoshi1" id="" class="text_box" placeholder="需突出针对性、注重可行性，建议不超过二百字"></textarea></div>
                </div>
                <div class="gjxg">
                    <div class="tit">改进效果</div>
                    <span class="count">({{content.xiaoguo1.length}}/200字)</span>
                    <div class="input_box"><textarea v-model="content.xiaoguo1" name="xiaoguo1" id="" class="text_box" placeholder="已经显现的实际效果，不是预测或估计成效，建议不超过二百字"></textarea></div>
                </div>
            </div>
        </div>
        <div class="action">
            <mt-button class="backbtn" type="default" @click="back()">返回</mt-button>
            <mt-button class="quickbtn" type="default" size="normal" @click="sheetVisible=true">快速跳转</mt-button>
            <mt-actionsheet :actions="data" v-model="sheetVisible">
            </mt-actionsheet>
        </div>
    </div>
</template>

<script>
export default {
    name: 'report',
    data() {
        return {
            sheetVisible: false,
            data: [
                {name:'校情核心指标体系',nav:'xqhxzb',method:this.action}, 
                {name:'学校基础数据分析',nav:'xxjcsj',method:this.action}, 
                {name:'专业发展数据分析',nav:'zyfzsj',method:this.action}, 
                {name:'课程建设数据分析',nav:'kcjssj',method:this.action}, 
                {name:'师资队伍数据分析',nav:'szdwsj',method:this.action}, 
                {name:'学生学习数据分析',nav:'xsxxsj',method:this.action},
                {name:'交叉分析',nav:'jcfx',method:this.action},
                {name:'自我诊断报告',nav:'zwzdbg',method:this.action}
            ],
            title:'',
            zonghe:false,
            zhuanye:[{label:'汽修',value:'汽修'},{label:'计算机',value:'计算机'},{label:'法律',value:'法律'},{label:'金融',value:'金融'},{label:'建筑',value:'建筑'}],
            zhibiao:[{label:'在校学生数',value:'在校学生数'},{label:'“双师型”教师数量',value:'“双师型”教师数量'},{label:'毕业生数量',value:'毕业生数量'},{label:'兼职老师数量',value:'兼职老师数量'},{label:'学校课程数',value:'学校课程数'},{label:'学校专业数',value:'学校专业数'},{label:'就业率',value:'就业率'},{label:'招生人数',value:'招生人数'},],
            peoples:[{label:'王二',value:'王二'},{label:'张三',value:'张三'},{label:'李四',value:'李四'},{label:'赵五',value:'赵五'},],
            name1:[
                {name:'张三',value:'1'},
                {name:'张三',value:'2'},
                {name:'张三',value:'3'},
                {name:'张三',value:'4'},
                {name:'张三',value:'5'},
                {name:'张三',value:'6'},
                {name:'张三',value:'7'},
                {name:'张三',value:'8'},
                {name:'张三',value:'9'},
                {name:'张三',value:'10'},
                {name:'张三',value:'11'},
                {name:'张三',value:'12'},
                {name:'张三',value:'13'},
                {name:'张三',value:'14'},
                {name:'张三',value:'15'},
                {name:'张三',value:'16'},
                {name:'张三',value:'17'},
            ],
            content:{
                yijian1:'',
                cuoshi1:'',
                xiaoguo1:'',
            },
            zhContent:{
                jielun:'',
                jianyi:'',
            }
        }
    },
    methods: {
        action(data){
            console.log(data)
            this.$router.push({
                path: '/secondmenu',
                name: 'secondMenu',
                query: {
                    nav: data.nav
                }
            })
        },
        back(){
            this.$router.go(-1)
        },
        fenpei(){

        },
        searchData(){
            
        }
    },
    mounted() {
        console.log(this.$route.query)
        this.title = this.$route.query.title + this.$route.query.name
        if(this.$route.query.name == '综合'){
            this.zonghe = true
            this.title = this.$route.query.name
        }
    },
}
</script>

<style>
#report{
    padding: 5px 15px;
}
#report .zwzd{
    display: flex;
    flex-direction: column;
}
#report .zwzd .tabletitle{
    width: 100%;
    text-align: center;
    margin: 0 auto;
    font-weight: 900;
}
#report .zwzd .btn{
    display: flex;
    margin: 10px 0;
}
#report .zwzd .btn .s_btn{
    margin-right: 40px;
}
#report .table{
    width: 100%;
    margin: 0 auto;
    border: 1px solid #e2e1e1;
}
#report .table .zdyj{
    border-bottom: 1px solid #e2e1e1;
    position: relative;
}
#report .table .gjcs{
    border-bottom: 1px solid #e2e1e1;
    position: relative;
}
#report .table .gjxg{
    position: relative;
}
#report .table .tit{
    height: 60px;
    line-height: 60px;
    padding-left: 20px;
    font-weight: 900;
    border-bottom: 1px solid #e2e1e1;
}
#report .table .input_box{
    flex: 1;
    height: 190px;
    margin: 10px;
    margin-left: 20px;
}
#report .table .input_box .text_box{
    width: 100%;
    height: 100%;
    resize: none;
    border: 0;
    padding: 0;
    outline: none;
}
#report .table .count{
    position: absolute;
    right: 5px;
    top: 40px;
    font-size: 14px;
    color: #aaaaaa; 
}
#report .action{
    padding: 20px;
    display: flex;
    justify-content: flex-end;
}
#report .action .quickbtn{
    margin-left: 20px;
}
</style>



