<template>
    <div id="mine">
        <div class="userInfo">
            <mt-cell title="姓名" value="李老师"></mt-cell>
            <mt-cell title="职位" value="教师"></mt-cell>
            <mt-cell title="手机号" value="15555555555"></mt-cell>
            <mt-cell title="修改密码" is-link @click.native="changePassword()">
            </mt-cell>
        </div>
    </div>
</template>

<script>
import { Cell,Button,MessageBox,Toast } from 'mint-ui';
export default {
    name: 'mine',
    data(){
        return{

        }
    },
    methods: {
        changePassword(){
            MessageBox.prompt('请输入密码', {
            inputValidator: (val) => {
                if (val === null) {
                    return true;    //初始化的值为null，不做处理刚打开MessageBox就会校验出错
                }
                return !(val.length < 6 || val.length > 8)
            }, inputErrorMessage: '密码长度必须在6~8位,'
            }).then((val) => {
                console.log(val)
                MessageBox.confirm('确定修改密码吗?').then(action => {
                    Toast({
                        message: '修改成功',
                        iconClass: 'el-icon-check'
                    });
                });
            }, () => {
                console.log('cancel')
            })

        }
    },
    mounted() {
        
    },
}
</script>

<style>
#mine{
    width: 100%;
    height: 100%;
}
#mine .userInfo{
    margin-top: 30px;
}
</style>


