            console.log(this.$route.query)
            localStorage.setItem("id",this.$route.query.id)
            var name = this.$route.query.name
            if(name == '基本指标'){
                this.name = 'jbzb'
            }else if(name == '重要指标'){
                this.name = 'zyzb'
            }else if(name == '基本办学条件'){
