<template>
    <div id="menu1">
        <div class="search">
            <mt-search class="searchipt" v-model="searchvalue" cancel-text="" placeholder="请输入您要搜索的内容"></mt-search>
            <mt-button class="searchbtn" @click="search()" type="primary" size="small">搜索</mt-button>
        </div>
        <div v-if="list" class="searchlist">
            <mt-cell v-for="(item,i) in list" :key="i" :title="item.name" :to="item.url" is-link></mt-cell>
        </div>
        <ul class="firstMenu">
            <li class="menuList"><router-link :to="{name:'secondMenu',query:{nav:'xqhxzb'}}" tag="span">校情核心指标体系</router-link></li>
            <li class="menuList"><router-link :to="{name:'secondMenu',query:{nav:'xxjcsj'}}" tag="span">学校基础数据分析</router-link></li>
            <li class="menuList"><router-link :to="{name:'secondMenu',query:{nav:'zyfzsj'}}" tag="span">专业发展数据分析</router-link></li>
            <li class="menuList"><router-link :to="{name:'secondMenu',query:{nav:'kcjssj'}}" tag="span">课程建设数据分析</router-link></li>
            <li class="menuList"><router-link :to="{name:'secondMenu',query:{nav:'szdwsj'}}" tag="span">师资队伍数据分析</router-link></li>
            <li class="menuList"><router-link :to="{name:'secondMenu',query:{nav:'xsxxsj'}}" tag="span">学生学习数据分析</router-link></li>
            <li class="menuList"><router-link :to="{name:'secondMenu',query:{nav:'jcfx'}}" tag="span">交叉分析</router-link></li>
            <li class="menuList"><router-link :to="{name:'secondMenu',query:{nav:'zwzdbg'}}" tag="span">自我诊断报告</router-link></li>
        </ul>
        <!-- <div class="back">
            <mt-button class="backbtn" @click="back()" type="primary" size="small">返回首页</mt-button>
        </div> -->
    </div>
</template>

<script>
import { Search,Button,Cell,Toast } from 'mint-ui';
export default {
    name: 'firstMenu',
    data(){
        return{
            searchvalue:'',
            list: [
                // {name:'列表1',url:'http://localhost:8080/#/'},       //跳转到图表页面
                // {name:'列表2',url:'http://localhost:8080/#/'}
            ],
            bgcolor: ['#768395','#7CB349','#358FE2','#505050','#E42739','#809295','#EF8629','#C6D90F'],
        }
    },
    methods: {
        search(){
            console.log(this.searchvalue)
            if(this.searchvalue == ''){
                Toast({
                    message: '搜索内容不能为空！',
                    position: 'top',
                    duration: 3000
                });
            }else{
                //向后台发起搜索请求赋值给list
            }
        },
        screen(){
            if(window.innerWidth <= '340'){
                var screen = document.getElementsByClassName('menuList')
                // console.log(screen)
                for(var i = 0;i < screen.length;i++){
                    screen[i].style.padding = '25px'
                }
            }
        }
    },
    mounted() {
        this.screen()
        var menuList = document.getElementsByClassName('menuList')
        // console.log(menuList)
        for(var i = 0;i < menuList.length;i++){
            menuList[i].style.background = this.bgcolor[i]
        }
    },
}
</script>

<style>
#menu1{
    width: 100%;
    height: 100%;
}
#menu1 .firstMenu{
    /* width: 100%; */
    height: auto;
    padding: 0 20px;
    margin-bottom: 20px;
    display: flex;
    flex-flow:row wrap;
    justify-content: space-between;
}
#menu1 .menuList{
    width: 70px;
    height: 70px;
    text-align: center;
    padding: 40px;
    margin-top: 20px;
    display:flex;
    align-items: center;
    border-radius: 4px;
    color: #ffffff;
}
#menu1 .menuList span{
    display: block;
    font-size: 16px;
}
#menu1 .search{
    display: flex;
}
#menu1 .searchipt{
    flex: 1;
}
#menu1 .searchbtn{
    width: 55px;
    height: 52px;
    background: #d9d9d9;
    border-radius: 0;
}
#menu1 .mint-searchbar{
    padding-right: 0;
}
#menu1 .mint-searchbar-cancel{
    margin: 0!important;
}
#menu1 .mint-search{
    height: auto;
}
/* #menu1 .back{
    padding: 0 20px;
    float: right;
    margin-bottom: 20px;
} */
</style>



