            this.$router.push({
                path: '/secondmenu',
                name: 'secondMenu',
                query: {
                    nav: data.nav
                }
            })
        },
        link(i){
            console.log(i)
            localStorage.setItem("tid",i.id)
            if(i.fourmenu){
                //跳转到第四级菜单
                this.$router.push({
                    path: '/fourthmenu',
                    name: 'fourthMenu',
                    query: {
                        id: i.id,
                        nid: i.fourmenu,
                        name: i.name,
                        reg: i.reg,
                        thirdId: this.nid,
                        thirdReg: this.reg,
                        thirdRoute: this.backUrl
                    }
                })
            }else if(i.card){
                //跳转到卡片页面
                this.$router.push({
                    path: '/card',
                    name: 'card',
                    query: {
                        id: i.id,
                        name: i.name,
                        reg: i.reg,
                    },
                })
            }else if(i.report){
                this.$router.push({
                    path: '/report',
                    name: 'report',
                    query: {
                        id: i.id,
                        title: i.title,
                        name: i.name,
                        reg: i.reg,
                    },
                })
            }else{
                //跳转到图表页面
                if(i.select){
                    this.$router.push({
                        path: '/chartsdata',
                        name: 'chartsdata',
                        query: {
                            id: i.id,
                            name: i.name,
                            reg: i.reg,
                            select: i.select
                        },
                    })
                }else{
                    this.$router.push({
                        path: '/chartsdata',
                        name: 'chartsdata',
                        query: {
                            id: i.id,
                            name: i.name,
                            reg: i.reg,
                        },
                    })
                }
                
            }
        },
        back(){
            this.$router.push({
                path: '/secondmenu',
                name: 'secondMenu',
                query: {
                    nav: this.backUrl
                }
            })
        },
        screen(){
            if(window.innerWidth <= '340'){
                var screen = document.getElementsByClassName('menuList')
                console.log(screen)
                for(var i = 0;i < screen.length;i++){
                    screen[i].style.padding = '25px'
                }
            }
        }
    },
    mounted() {
        console.log(this.$route.query)
        this.reg = this.$route.query.reg
        this.nid = this.$route.query.nid
        this.backUrl = this.$route.query.route
        if(this.nid){
            this.menulist = this.navlist[this.nid-1].navName
            console.log(this.menulist)
            for(var i = 0;i < this.navlist[this.nid-1].navName.length;i++){     //将reg数据赋值给navlist
                this.navlist[this.nid-1].navName[i].reg = this.reg
            }
        }
        var _this = this
        setTimeout(function () {
            var menuListNum = document.getElementsByClassName('menuList')
            var thirdmenu = document.getElementsByClassName('thirdmenu')[0]
            var hiddenLi = document.createElement("li")
            var attr = document.createAttribute('style')
            attr.value = "width:150px;height:150px;margin-top:20px;visibility:hidden;"
            hiddenLi.setAttributeNode(attr)
            console.log(menuListNum,thirdmenu)
            if(menuListNum.length % 2 == 1){
                thirdmenu.appendChild(hiddenLi)
            }
            for(var i = 0;i < menuListNum.length;i++){
                menuListNum[i].style.background = _this.bgcolor[i]
            }
            _this.screen()
        },10)
    },
