.menuList{
    width: 70px;
    height: 70px;
    text-align: center;
    padding: 40px;
    margin-top: 20px;
    display:flex;
    align-items: center;
    border-radius: 4px;
    color: #ffffff;
}
.menuList span{
    display: block;
    width: 100%;
    text-align: center;
    font-size: 16px;
}
.action{
    padding: 20px;
    display: flex;
    justify-content: flex-end;
}
.action .quickbtn{
    margin-left: 20px;
}
</style>



