<template>
    <div id="menu4">
        <ul class="fourthmenu">
            <li v-if="menulist" class="menuList" v-for="(item,k) in menulist" :key="k" @click="link(item)">
                <span>{{item.name}}</span>
            </li>
        </ul>
        <div class="action">
            <mt-button class="backbtn" type="default" @click="back()">返回</mt-button>
            <mt-button class="quickbtn" type="default" size="normal" @click="sheetVisible=true">快速跳转</mt-button>
            <mt-actionsheet :actions="data" v-model="sheetVisible">
            </mt-actionsheet>
        </div>
    </div>
</template>

<script>
export default {
    name: 'fourthMenu',
    data(){
        return{
            sheetVisible: false,
            data: [
                {name:'校情核心指标体系',nav:'xqhxzb',method:this.action}, 
                {name:'学校基础数据分析',nav:'xxjcsj',method:this.action}, 
                {name:'专业发展数据分析',nav:'zyfzsj',method:this.action}, 
                {name:'课程建设数据分析',nav:'kcjssj',method:this.action}, 
                {name:'师资队伍数据分析',nav:'szdwsj',method:this.action}, 
                {name:'学生学习数据分析',nav:'xsxxsj',method:this.action},
                {name:'交叉分析',nav:'jcfx',method:this.action},
                {name:'自我诊断报告',nav:'zwzdbg',method:this.action}
            ],
            bgcolor: ['#768395','#7CB349','#358FE2','#505050','#E42739','#809295','#EF8629','#C6D90F'],
            menulist: [],
            navlist: [
                {
                    navName:[
                        {name:'办学定位',id:'1',reg:'bxdw',route:'xqhxzb'},
                        {name:'人才培养目标',id:'2',reg:'rcpymb'},
                        {name:'素质教育',id:'3',reg:'szjy',route:'xqhxzb'},
                    ]
                },
                {
                    navName:[
                        {name:'专业建设状态',id:'4',reg:'zyjszt'},
                        {name:'课程建设状态',id:'5',reg:'kcjszt'},
                        {name:'课堂教学与实践教学状态',id:'6',reg:'ktjxysjjx'},
                    ]
                },
                {
                    navName:[
                        {name:'专业教师队伍建设状态',id:'7',reg:'zyjsdw'},
                        {name:'兼职教师队伍建设状态',id:'8',reg:'jzjsdw'},
                    ]
                },
                {
                    navName:[
                        {name:'教学投入及基础设施建设状态',id:'9',reg:'jxtrjjcss'},
                        {name:'教学资源建设状态',id:'10',reg:'jxzyjszt'},
                    ]
                },
                {
                    navName:[
                        {name:'校企合作状态',id:'11',reg:'xqhzzt'},
                        {name:'学校管理制度建设与运行状态',id:'12',reg:'xxglzdjsyyxzt'},
                        {name:'质量监控状态',id:'13',reg:'zljkzt'},
                    ]
                },
                {
                    navName:[
                        {name:'学生反馈',id:'14',reg:'xsfk'},
                        {name:'用人方反馈',id:'15',reg:'yrffk'},
                        {name:'其他',id:'16',reg:'qt'},
                    ]
                },
                {
                    navName:[
                        {name:'实训设备',id:'17',reg:'sxsb'},
                        {name:'学校校舍、占地面积等',id:'18',reg:'xxxszdmj'},
                        {name:'图书馆藏书同比图',id:'19',reg:'tsgcstbt'},
                        {name:'教学设备',id:'20',reg:'jxsb'},
                    ]
                },
                {
                    navName:[
                        {name:'办学定位',id:'21',reg:'bxlnbxdw',title:'办学理念--',report:true},
                        {name:'人才培养目标',id:'22',reg:'bxlnrcpymb',title:'办学理念--',report:true},
                        {name:'素质教育',id:'23',reg:'bxlnszjy',title:'办学理念--',report:true},
                    ]
                },
                {
                    navName:[
                        {name:'专业建设状态',id:'24',reg:'jxgzztzyjs',title:'教学工作状态--',report:true},
                        {name:'课程建设状态',id:'25',reg:'jxgzztkcjs',title:'教学工作状态--',report:true},
                        {name:'课堂教学与实践教学状态',id:'26',reg:'jxgzztktjxysjjx',title:'教学工作状态--',report:true},
                    ]
                },
                {
                    navName:[
                        {name:'专任教师队伍建设状态',id:'27',reg:'szdwjszrjsdw',title:'师资队伍建设状态--',report:true},
                        {name:'兼职教师队伍建设状态',id:'28',reg:'szdwjzjsdw',title:'师资队伍建设状态--',report:true},
                    ]
                },
                {
                    navName:[
                        {name:'教学投入及基础设施建设状态',id:'29',reg:'zyjsjxtrjjcss',title:'资源建设状态--',report:true},
                        {name:'教学资源建设状态',id:'30',reg:'zyjsjxzy',title:'资源建设状态--',report:true},
                    ]
                },
                {
                    navName:[
                        {name:'校企合作状态',id:'31',reg:'zdjsyyxxqhz',title:'制度建设与运行状态--',report:true},
                        {name:'学校管理制度建设与运行状态',id:'32',reg:'zdjsyyxxxglzdjs',title:'制度建设与运行状态--',report:true},
                        {name:'质量监控状态',id:'33',reg:'zdjsyyxzljk',title:'制度建设与运行状态--',report:true},
                    ]
                },
                {
                    navName:[
                        {name:'学生反馈',id:'34',reg:'xqffkxsfk',title:'需求方反馈--',report:true},
                        {name:'用人方反馈',id:'35',reg:'xqffkyrffk',title:'需求方反馈--',report:true},
                        {name:'其他',id:'36',reg:'xqffkqt',title:'需求方反馈--',report:true},
                    ]
                },
            ],
            reg: '',
            id: '',
            nid: '',
            backurl: {
                nid: '',
                reg: '',
                route: ''
            }
        }
    },
    methods: {
        action(data){
            console.log(data)
            this.$router.push({
                path: '/secondmenu',
                name: 'secondMenu',
                query: {
                    nav: data.nav
                }
            })
        },
        back(){
            this.$router.push({
                path: '/thirdmenu',
                name: 'thirdMenu',
                query: {
                    nid: this.backurl.nid,
                    reg: this.backurl.reg,
                    route: this.backurl.route
                }
            })
        },
        link(i){
            console.log(i)
            if(i.report){
                this.$router.push({
                    path: '/report',
                    name: 'report',
                    query: {
                        id: i.id,
                        title: i.title,
                        name: i.name,
                        reg: i.reg,
                    },
                })
            }else{
                //跳转到图表页面
                this.$router.push({
                    path: '/chartsdata',
                    name: 'chartsdata',
                    query: {
                        id: i.id,
                        name: i.name,
                        reg: i.reg,
                    },
                })
            }
        },
        screen(){
            if(window.innerWidth <= '340'){
                var screen = document.getElementsByClassName('menuList')
                console.log(screen)
                for(var i = 0;i < screen.length;i++){
                    screen[i].style.padding = '25px'
                }
            }
        }
    },
    mounted() {
        this.screen()
        console.log(this.$route.query)
        // localStorage.setItem("tid",this.$route.query.id)
        this.reg = this.$route.query.reg
        this.id = this.$route.query.id
        this.nid = this.$route.query.nid
        this.backurl.nid = this.$route.query.thirdId
        this.backurl.reg = this.$route.query.thirdReg
        this.backurl.route = this.$route.query.thirdRoute
        if(this.nid){
            this.menulist = this.navlist[this.nid-1].navName
            console.log(this.menulist)
            for(var i = 0;i < this.navlist[this.nid-1].navName.length;i++){
                this.navlist[this.nid-1].navName[i].reg = this.reg
            }
        }
        var _this = this
        setTimeout(function () {
            var menuListNum = document.getElementsByClassName('menuList')
            var thirdmenu = document.getElementsByClassName('fourthmenu')[0]
            var hiddenLi = document.createElement("li")
            var attr = document.createAttribute('style')
            attr.value = "width:150px;height:150px;margin-top:20px;visibility:hidden;"
            hiddenLi.setAttributeNode(attr)
            console.log(menuListNum,thirdmenu)
            if(menuListNum.length % 2 == 1){
                thirdmenu.appendChild(hiddenLi)
            }
            for(var i = 0;i < menuListNum.length;i++){
                menuListNum[i].style.background = _this.bgcolor[i]
            }
            _this.screen()
        },10)
    },
}
</script>

<style scoped>
#menu4{
    width: 100%;
    height: 100%;
}
.fourthmenu{
    /* width: 100%; */
    height: auto;
    padding: 0 20px;
    margin-bottom: 20px;
    display: flex;
    flex-flow:row wrap;
    justify-content: space-between;
}
.menuList{
    width: 70px;
    height: 70px;
    text-align: center;
    padding: 40px;
    margin-top: 20px;
    display:flex;
    align-items: center;
    border-radius: 4px;
    color: #ffffff;
}
.menuList span{
    display: block;
    width: 100%;
    text-align: center;
    font-size: 16px;
}
.action{
    padding: 20px;
    display: flex;
    justify-content: flex-end;
}
.action .quickbtn{
    margin-left: 20px;
}
</style>



