<template>
    <div id="menu2">
        <ul class="senondmenu">
            <li v-if="menulist" class="menuList" v-for="(item,k) in menulist" :key="k">
                <router-link :to="{name:'thirdMenu',query:{nid:item.id,reg:item.reg,route:item.route}}" tag="span">{{item.routeName}}</router-link>
            </li>
        </ul>
        <div class="action">
            <mt-button class="backbtn" type="default" @click="back()">返回</mt-button>
            <mt-button class="quickbtn" type="default" size="normal" @click="sheetVisible=true">快速跳转</mt-button>
            <mt-actionsheet :actions="data" v-model="sheetVisible">
            </mt-actionsheet>
        </div>
    </div>
</template>

<script>
import { Button,Actionsheet } from 'mint-ui';
export default {
    name: 'secondMenu',
    data() {
        return {
            // pickerValue:'',
            sheetVisible: false,
            data: [
                {name:'校情核心指标体系',nav:'xqhxzb',method:this.action}, 
                {name:'学校基础数据分析',nav:'xxjcsj',method:this.action}, 
                {name:'专业发展数据分析',nav:'zyfzsj',method:this.action}, 
                {name:'课程建设数据分析',nav:'kcjssj',method:this.action}, 
                {name:'师资队伍数据分析',nav:'szdwsj',method:this.action}, 
                {name:'学生学习数据分析',nav:'xsxxsj',method:this.action},
                {name:'交叉分析',nav:'jcfx',method:this.action},
                {name:'自我诊断报告',nav:'zwzdbg',method:this.action}
            ],
            bgcolor: ['#768395','#7CB349','#358FE2','#505050','#E42739','#809295','#EF8629','#C6D90F'],
            menulist: [],
            navlist:{
                xqhxzb:[{
                    routeName: '中职学校标准',
                    id: '1',
                    route: 'xqhxzb',    //路由名称
                    reg: 'xqhxzb',      //获取图表数据要传的参数
                },{
                    routeName: '中职能力评估',
                    id: '2',
                    route: 'xqhxzb',
                    reg: 'xqhxzb'
                },{
                    routeName: '中职诊改点',
                    id: '3',
                    route: 'xqhxzb',
                    reg: 'xqhxzb'
                }],
                xxjcsj:[{
                    routeName: '基本信息',
                    id: '4',
                    route: 'xxjcsj',
                    reg: 'xxjcsj'
                },{
                    routeName: '办学经费',
                    id: '5',
                    route: 'xxjcsj',
                    reg: 'xxjcsj'
                },{
                    routeName: '师资队伍',
                    id: '6',
                    route: 'xxjcsj',
                    reg: 'xxjcsj'
                },{
                    routeName: '专业特色',
                    id: '7',
                    route: 'xxjcsj',
                    reg: 'xxjcsj'
                },{
                    routeName: '课程概况',
                    id: '8',
                    route: 'xxjcsj',
                    reg: 'xxjcsj'
                },{
                    routeName: '学生工作',
                    id: '9',
                    reg: 'xxjcsj'
                },{
                    routeName: '党建工作',
                    id: '10',
                    route: 'xxjcsj',
                    reg: 'xxjcsj'
                }],
                zyfzsj: [{
                    routeName: '学生情况',
                    id: '11',
                    route: 'zyfzsj',
                    reg: 'zyfzsj'
                },{
                    routeName: '师资结构',
                    id: '12',
                    route: 'zyfzsj',
                    reg: 'zyfzsj'
                },{
                    routeName: '就业情况',
                    id: '13',
                    route: 'zyfzsj',
                    reg: 'zyfzsj'
                },{
                    routeName: '教学实训',
                    id: '14',
                    route: 'zyfzsj',
                    reg: 'zyfzsj'
                }],
                kcjssj: [{
                    routeName: '在线课堂分析',
                    id: '15',
                    route: 'kcjssj',
                    reg: 'kcjssj'
                },{
                    routeName: '线下课堂分析',
                    id: '16',
                    route: 'kcjssj',
                    reg: 'kcjssj'
                }],
                szdwsj: [{
                    routeName: '人员情况分析',
                    id: '17',
                    route: 'szdwsj',
                    reg: 'szdwsj'
                },{
                    routeName: '专业技能分析',
                    id: '18',
                    route: 'szdwsj',
                    reg: 'szdwsj'
                }],
                xsxxsj: [{
                    routeName: '招生工作',
                    id: '19',
                    route: 'xsxxsj',
                    reg: 'xsxxsj'
                },{
                    routeName: '就业升学',
                    id: '20',
                    route: 'xsxxsj',
                    reg: 'xsxxsj'
                },{
                    routeName: '在校学习',
                    id: '21',
                    route: 'xsxxsj',
                    reg: 'xsxxsj'
                }],
                jcfx: [{
                    routeName: '交叉分析',
                    id: '22',
                    route: 'jcfx',
                    reg: 'jcfx'
                }],
                zwzdbg:[{
                    routeName: '自我诊断',
                    id: '23',
                    route: 'zwzdbg',
                    reg: 'zwzdbg'
                }],
            },
            nav: null,
        }
    },
    methods: {
        action(data){
            console.log(data)
            this.$router.push({
                path: '/secondmenu',
                name: 'secondMenu',
                query: {
                    nav: data.nav
                }
            })
            this.$router.go(0)
        },
        back(){
            //返回到一级菜单
            this.$router.push({
                path: '/firstmenu',
                name: 'firstmenu',
                query: {
                    
                }
            })
        },
        screen(){
            if(window.innerWidth <= '340'){
                var screen = document.getElementsByClassName('menuList')
                // console.log(screen)
                for(var i = 0;i < screen.length;i++){
                    screen[i].style.padding = '25px'
                }
            }
        }
    },
    mounted() {
        console.log(this.$route)
        this.nav = this.$route.query.nav
        if(this.nav == 'xqhxzb'){
            this.menulist = this.navlist.xqhxzb
        }else if(this.nav == 'xxjcsj'){
            this.menulist = this.navlist.xxjcsj
        }else if(this.nav == 'zyfzsj'){
            this.menulist = this.navlist.zyfzsj
        }else if(this.nav == 'kcjssj'){
            this.menulist = this.navlist.kcjssj
        }else if(this.nav == 'szdwsj'){
            this.menulist = this.navlist.szdwsj
        }else if(this.nav == 'xsxxsj'){
            this.menulist = this.navlist.xsxxsj
        }else if(this.nav == 'jcfx'){
            this.menulist = this.navlist.jcfx
        }else if(this.nav == 'zwzdbg'){
            this.menulist = this.navlist.zwzdbg
        }
        var _this = this
        setTimeout(function () {
            var menuListNum = document.getElementsByClassName('menuList')
            var senondmenu = document.getElementsByClassName('senondmenu')[0]
            var hiddenLi = document.createElement("li")
            var attr = document.createAttribute('style')
            attr.value = "width:150px;height:150px;margin-top:20px;visibility:hidden;"
            hiddenLi.setAttributeNode(attr)
            console.log(menuListNum,senondmenu)
            if(menuListNum.length % 2 == 1){
                senondmenu.appendChild(hiddenLi)
            }
            for(var i = 0;i < menuListNum.length;i++){
                // console.log('1',_this.bgcolor)
                menuListNum[i].style.background = _this.bgcolor[i]
            }
            _this.screen()
        },10)
    },
}
</script>

<style scoped>
#menu2{
    width: 100%;
    height: 100%;
}
.senondmenu{
    /* width: 100%; */
    height: auto;
    padding: 0 20px;
    margin-bottom: 20px;
    display: flex;
    flex-flow:row wrap;
    justify-content: space-between;
}
.menuList{
    width: 70px;
    height: 70px;
    text-align: center;
    padding: 40px;
    margin-top: 20px;
    display:flex;
    align-items: center;
    border-radius: 4px;
    color: #ffffff;
}
.menuList span{
    display: block;
    font-size: 16px;
}
.action{
    padding: 20px;
    display: flex;
    justify-content: flex-end;
}
.action .quickbtn{
    margin-left: 20px;
}
</style>



